* {
  box-sizing: border-box;
  margin: 0;
  border: 1px solid blueviolet; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

/* HEADER **************************/
header {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px; }
  header h1 {
    flex: 1; }
  header nav ul {
    display: flex;
    flex-direction: row;
    align-self: flex-end;
    list-style-type: none; }
    header nav ul li {
      margin: 0 10px; }
    header nav ul a {
      color: #111;
      text-decoration: none; }

/* Main Content Section ****************/
.content {
  display: flex;
  flex-direction: row;
  flex: 2;
  margin-top: 25px; }
  .content .leftCol {
    display: flex;
    flex-direction: column;
    flex: 1 1 10%;
    margin: 1%;
    padding: 1%; }
  .content .centerCol {
    display: flex;
    flex-direction: column;
    flex: 2 1 58%;
    margin: 1%;
    padding: 1%; }
  .content .rightCol {
    display: flex;
    flex-direction: column;
    flex: 1 1 26%;
    margin: 1%;
    padding: 1%; }

/* Article Section *************************/
.articles {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin: 50px auto;
  padding: 10%; }
  .articles .article1 {
    display: flex;
    flex-direction: column;
    flex: 1 0 50%; }
  .articles .article2 {
    display: flex;
    flex-direction: column;
    flex: 1 0 50%; }

footer {
  display: flex;
  justify-content: center;
  padding: 10px; }
